// Card CSS
.card {
  filter: drop-shadow(3px 4px 10px rgba(232, 232, 232, 0.8));
  height: auto;
  margin-bottom: -100px;


  &__img-box {
    position: relative;
  }

  &__img {
    border-radius: 10px 10px 0 0;
    width: 100%;
  }

  &__location {
    position: absolute;
    top: 16px;
    left: 8px;
    margin: 0 8px 0 0;
    border-radius: 10px;
    padding: 4px 8px 4px 28px;
    font-weight: 400;
    font-size: 16px;
    line-height: 110%;
    color: var(--colour-white);
    background-color: var(--colour-white-bg);

    &::before {
      content: '';
      position: absolute;
      background-image: url(/img/icon--location-card.svg);
      background-position: center;
      background-repeat: no-repeat;
      background-size: cover;
      width: 18px;
      height: 18px;
      left: 8px;
      top: 50%;
      transform: translateY(-50%);
    }

  }

  &__category {
    position: absolute;
    top: 16px;
    left: 8px;
    margin: 0 8px 0 0;
    border-radius: 10px;
    padding: 8px 16px;
    font-weight: 400;
    font-size: 16px;
    line-height: 110%;
    color: var(--colour-white);
    background: var(--colour-gradient-purple);

  }

  &__bottom {
    padding: 10px;
    transform: translateY(-100px);
    background-color: var(--colour-white);
    border-radius: 10px;
    text-align: left;
  }

  &__title {
    margin: 0;
    padding-bottom: 4px;
    font-weight: 600;
    font-size: 20px;
    line-height: 110%;
    color: var(--colour-title);
  }

  &__date {
    margin: 0;
    font-weight: 400;
    font-size: 16px;
    line-height: 110%;
    color: var(--colour-text);
  }

  &__info {
    @include flex;

    align-items: center;
    justify-content: start;
    font-weight: normal;
    font-size: 16px;
    line-height: 110%;
    color: var(--colour-text);
    margin-top: 16px;
    margin-bottom: 10px;

  }

  &__rating {
    position: relative;
    margin: 0 8px 0 0;
    padding-left: 20px;

    &::before {
      content: '';
      position: absolute;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      background-image: url(/img/icon--rating-card.svg);
      background-position: center;
      background-repeat: no-repeat;
      background-size: contain;
      width: 16px;
      height: 16px;
    }

  }

  &__type {
    position: relative;
    margin: 0 8px 0 0;
    padding-left: 20px;

    &::before {
      content: '';
      position: absolute;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      background-image: url(/img/icon--type-card.svg);
      background-position: center;
      background-repeat: no-repeat;
      background-size: contain;
      width: 16px;
      height: 16px;
    }

  }

  &__players {
    position: relative;
    padding-left: 20px;
    margin: 0;

    &::before {
      content: '';
      position: absolute;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      background-image: url(/img/icon--player-card.svg);
      background-position: center;
      background-repeat: no-repeat;
      background-size: contain;
      width: 14px;
      height: 14px;
    }

  }

  &__cta {
    display: block;
    text-align: center;
    font-size: 16px;
    line-height: 16px;
    padding: 14px 30px;
  }

  &__scroll {
    border-radius: 10px;
    width: 360px;
    height: 5px;
    z-index: 1000;
  }

  &__nav {
    @include flex-all-sb;
    z-index: 1000;
  }

  &__btn {
    @include bg-cover;
    z-index: 1000;

    @include small-tablet {
      display: none;
    }


    width: 25px;
    height: 25px;

      &--prev {
        background-image: url("../img/slider--prev.svg");
        z-index: 1000;
      }
      &--next {
        background-image: url("../img/slider--next.svg");
        z-index: 1000;

      }

      &:not(:last-child) {
        margin-right: 10px;
      }
  }

}

.swiper-scrollbar-drag {
  background-color: var(--colour-green);
}
