// Footer CSS

.footer {
  margin-top: 80px;
  padding: 40px 0 30px 0;
  border-radius: 10px 10px 0 0;
  background: var(--colour-gradient-green);

  @include big-desktop-min {
    margin: 80px 20px 0 20px;
  }

  @include tablet {
    border-radius: 0;
  }

  &__container {
    @include flex-all-sb;

    @include md-mobile {
      flex-direction: column;
    }

  }

  &__logo {
    filter: brightness(0) invert(1);
  }

  &__copy {
    font-weight: 400;
    font-size: 16px;
    line-height: 110%;
    color: var(--colour-white);
  }

}

