// Benefits CSS

.benefits {

  padding-top: 80px;

  &__container {
    @include flex-all-sb;

    @include mid-tablet {
      flex-direction: column;
      justify-content: center;
    }

  }

  &__container + .benefits__container {
    padding-top: 80px;
  }

  &__left {
    width: 48%;

    @include mid-tablet {
      max-width: 590px;
      width: 100%;
    }

  }

  &__right {
    max-width: 430px;
    width: 48%;

    @include mid-tablet {
      max-width: 590px;
      width: 100%;
    }
  }

  &__title {
    padding-bottom: 30px;

    @include mid-tablet {
      padding-bottom: 10px;
    }
  }

  &__text {
    padding-bottom: 30px;
    @include mid-tablet {
      padding-bottom: 20px;
    }
  }

  &__cta {
    display: inline-block;
    margin-top: 20px;

    @include mid-tablet {
      margin: 0;
    }
  }

}

@include mid-tablet {

  .benefits__container + .benefits__container > .benefits__left {
    order: 1;
  }
  .benefits__container + .benefits__container > .benefits__right {
    order: 2;
  }


}

