// Reviews CSS
.reviews {

  &__container {
    @include flex-all-sb;

    @include big-mobile {
      justify-content: center;
    }

  }

  &__container + .benefits__container {
    padding-top: 80px;
  }

  &__left {
    width: 48%;

    @include big-mobile {
      display: none;
    }

  }

  &__right {
    max-width: 430px;
    width: 48%;

    @include big-mobile {
      max-width: 600px;
      width: 100%;
    }
  }

  &__title {
    padding-bottom: 30px;
  }

  &__text {
    font-size: 20px;
    line-height: 1.4em;
    padding-bottom: 30px;
  }

  &__cta {
    display: inline-block;
    margin-top: 20px;
  }

  &__avatar {
    @include flex;

    justify-content: start;
    align-items: center;
    margin-top: 16px;
  }

  &__info {
    margin-left: 16px;
  }

  &__name {
    margin: 0;
    padding: 0;
    font-weight: 600;
    font-size: 20px;
    line-height: 16px;
    color: var(--colour-title);
  }

  &__position {
    margin: 0;
    padding: 8px 0 0 0;
    font-weight: 400;
    font-size: 16px;
    line-height: 16px;
    color: var(--colour-text);
  }

}
