
// Root
:root {
  // Container
  --font-family: "SFProDisplay", sans-serif;
  --content-width: 1200px;
  --content-fluid-width: 1400px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --container-fluid-width: calc(var(--content-fluid-width) + (var(--container-offset) * 2));

  // Colours
  --colour-bg: #F6F5F9;
  --colour-white: #FFFFFF;
  --colour-white-bg: rgba(246, 245, 249, 0.35);
  --colour-title: #1C1C1C;
  --colour-text: #9B999E;
  --colour-overlay: rgba(246, 245, 249, 0.35);
  --colour-yellow: #FFC807;
  --colour-green: #7ABE70;
  --colour-blue: #1F4AB9;
  --colour-red: #D5011D;
  --colour-gradient-green: linear-gradient(101.68deg, #0BAB64 1.15%, #3BB78F 99.58%);
  --colour-gradient-purple: linear-gradient(120.97deg, #1F4AB9 2.75%, #6149AF 98.66%);
}
