// CTA Buttons CSS

.section {

  &__cta {
    font-weight: 400;
    font-size: 20px;
    line-height: 16px;
    color: var(--colour-white);

    padding: 16px 30px;
    border-radius: 5px;
    background-color: var(--colour-green);
    box-shadow: 2px 4px 10px rgba(122, 190, 112, 0.5);
    transition: box-shadow 0.3s ease-in-out;

    &:hover {
      box-shadow: 2px 4px 10px rgba(122, 190, 112, 0);
    }

    @include mid-mobile  {
      font-size: 16px;
    }

  }

}
