.hero{
  @include flex;

  align-items: flex-end;
  justify-content: space-between;


  padding-top: 100px;
  height: 100%;
  margin: 20px auto;
  border-radius: 10px;
  background: var(--colour-gradient-green);

  @include big-desktop-min {
    margin: 20px;
  }

  @include tablet {
    margin: 0;
    border-radius: 0;
  }

  &__container {
    @include flex-all-sb;
    width: 100%;
  }

  &__content {
    max-width: 440px;
    color: var(--colour-white);

    @include mid-tablet {
      max-width: 600px;
      width: 100%;
      margin: 0 auto;

    }
  }

  &__title {
    margin: 0;
    font-weight: 700;
    font-size: 56px;
    line-height: 1.1em;

    @include md-mobile {
      font-size: 36px;
    }
  }

  &__text {
    font-weight: 400;
    font-size: 20px;
    line-height: 1.4em;
  }

  &__right {
    margin-left: 60px;

    @include mid-tablet {
      margin-left: 0;
      display: none;
    }

  }

  &__left {

    @include mid-tablet {
      width: 100%;

    }

  }

  &__sponsors {
    position: absolute;
    left: 50%;
    transform: translate(-50%, 50px);

    max-width: 580px;
    margin-top: 20px;
    padding: 20px 20px;
    border-radius: 10px;
    background-color: var(--colour-white);
    filter: drop-shadow(3px 4px 10px rgba(232, 232, 232, 0.8));

    @include mid-tablet {
      position: relative;
      max-width: 600px;
      margin: 40px auto;
      left: 0;
      transform: translate(0);
    }

    @include mobile {
      border-radius: 0;
      margin: 20px 0 0 0;
      width: 100%;
    }

  }

  &__sponsors-box:not(:last-child) {
    margin-right: 20px;
  }

  &.container-fluid {

    @include tablet {
      padding: 0;
    }

    @include mid-tablet {
      padding: 40px 0 0 0;
      flex-direction: column;
    }

  }

}

.hero-hide {
  transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
}

.hero-hide-toggle {
  opacity: 0;
  visibility: none;
  z-index: -1;
}

.sponsors-hide {
  opacity: 1;
  visibility: visible;
  transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
}

.sponsors-hide-toggle {
  opacity: 0;
  visibility: none;
}
