// Filter CSS

.filter {
  position: relative;
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  gap: 20px;

  &__btn--close {
  display: none;

  @include mid-tablet {
    display: block;
  }

    & .burger__line {
    background-color: var(--colour-title);
    right: 15px;
    top: 15px;
    left: auto;
  }
}

  &__container {
    grid-column: 3 span;

    @include mid-tablet {
      position: absolute;
      z-index: 1000;
      top: 50px;
      padding: 50px 20px;
      background: var(--colour-bg);
      border-radius: 10px;
      transform: translateX(-200%);
      transition: transform 0.3s ease-in-out;
    }

    &--active {
      transform: translateX(0);
    }

  }

  &__form {
    @include mid-tablet {
      max-width: 285px;
      width: 100%;
    }
  }

  &__content {
    @include flex;

    justify-content: space-between;
    align-items: flex-end;
    margin-bottom: 20px;
  }

  &__title {
    font-weight: 600;
    font-size: 32px;
    line-height: 1.1em;
    color: var(--colour-title);
    margin: 0;
  }

  &__clear {
    font-weight: 400;
    font-size: 16px;
    line-height: 1.1em;
    color: var(--colour-text);
  }

  &__wrapper {
    padding: 10px;
    border-radius: 10px;
    background-color: var(--colour-white);
    box-shadow: 3px 4px 10px rgba(232, 232, 232, 0.8);

    &:not(:last-child) {
      margin-bottom: 20px;
    }
  }

  &__name {
    position: relative;
    font-weight: 600;
    font-size: 20px;
    line-height: 110%;
    color: var(--colour-title);
    margin: 0;

    &::after {
      content: '';
      position: absolute;
      width: 24px;
      height: 24px;
      right: 0;
      background-image: url('../img/filter--arrow.svg');
      background-size: contain;
      background-repeat: no-repeat;
      background-position: center center;
      transition: transform 0.3s ease-in-out;

    }

    &--active::after {
      transform: rotate(180deg);
    }

  }

  &__box {
    margin-top: 16px;
    display: flex;
    flex-wrap: wrap;

    &--hidden {
      display: none;
    }
  }

  &__cards {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: 20px;

  }

  &-cards{

    &__container {
      grid-column: 9 span;
      padding: 0;

      @include mid-tablet {
        grid-column: 12 span;
        }

    }

    &__content {
      display: flex;
      justify-content: flex-start;
      margin-bottom: 20px;
    }

  }

  &-select {
    margin-right: 20px;

    &__box {
      font-weight: 400;
      font-size: 16px;
      line-height: 16px;
      color: var(--colour-text);
      padding: 8px;
      border: none;
      border-radius: 5px;
      background-color: var(--colour-white);
      box-shadow: 3px 4px 10px rgba(232, 232, 232, 0.8);
    }

  }

  &-cards__btn {
    display: none;
    font-weight: 400;
    font-size: 14px;
    line-height: 1.1em;
    color: var(--colour-text);
    padding: 8px 16px;
    border: none;
    border-radius: 5px;
    background-color: var(--colour-white);
    box-shadow: 3px 4px 10px rgba(232, 232, 232, 0.8);

    @include mid-tablet {
      display: block;
    }

  }

  &__item {
    grid-column: 4 span;

    @include mid-tablet {
      grid-column: 4 span;
    }

    @include small-tablet {
      grid-column: 6 span;
    }

    @include mobile {
      grid-column: 12 span;
    }
  }

}
