// Section Popular CSS

.popular {

  padding: 80px 0;
  text-align: center;

  &__content {
    margin: 0 auto;
    max-width: 720px;
    width: 100%;
  }

  &__title {
    padding-bottom: 16px;
  }

  &__swiper {
    padding-top: 40px;

  }

  &__wrapper {
    height: 380px;
    z-index: -1;

    @include big-tablet {
      height: 360px;
    }

    @include md-mobile {
      height: 340px;
    }

  }

  &__item {
    height: 360px;
  }

}
