// Single Page CSS

.single {

  &__hero {
    position: relative;
  }

  &__img {
    border-radius: 10px;
    min-height: 240px;
  }

  &__icon {
    position: absolute;
    top: 20px;
    right: 20px;
    height: 40px;
    width: 40px;
    padding: 8px;
    border-radius: 50%;
    background-color: var(--colour-bg);
    cursor: pointer;
  }

  &__icon-svg{
    fill: var(--colour-text);
    transition: fill 0.3s ease-in-out;
  }
  &__icon-svg:hover {
    fill: red;
  }

  &__content {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    width: 100%;
    max-width: --container-width;
    min-width: 320px;
    padding-top: 20px;
  }

  &__header {
    grid-column: 9 span;

    @include tablet {
      grid-column: 8 span;
    }

    @include small-tablet {
      grid-column: 12 span;
      margin: 0 0 60px 0;
    }

  }

  &__title {
    font-weight: 600;
    font-size: 40px;
    line-height: 110%;
    color: var(--colour-title);
    margin: 0;
    padding-bottom: 8px;

    @include small-tablet {
      font-size: 32px
    }

  }

  &__box {

    @include flex;

    align-items: center;
    justify-content: flex-start;

    @include mid-mobile {
      flex-wrap: wrap;
      gap: 10px;
    }
  }

  &__item {
    font-weight: 500;
    font-size: 20px;
    line-height: 110%;
    color: var(--colour-text);
    margin: 0;
  }

  &__item:not(:last-child) {
    position: relative;
    padding-right: 40px;
  }

  &__item:not(:last-child)::after{
    content: '';
    position: absolute;
    top: 50%;
    right: 20px;
    height: 16px;
    width: 2px;
    border-radius: 2px;
    background-color: var(--colour-text);
    transform: translateY(-50%);

    @include mid-mobile {
      width: 0;
      display: none;
    }

  }

  &__rating {
    position: relative;
    padding-left: 25px;

    &::before {
      content: '';
      position: absolute;
      background-image: url("../img/rating--star.svg");
      height: 20px;
      width: 20px;
      top: 50%;
      left: 0;
      transform: translateY(-50%);
    }

  }

  &__book {
    grid-column: 3 span;
    background-color: var(--colour-white);
    box-shadow: 3px 4px 10px rgba(232, 232, 232, 0.8);
    border-radius: 5px;
    padding: 16px;

    @include tablet {
      grid-column: 4 span;
    }

    @include small-tablet  {
      grid-column: 12 span;
      max-width: 320px;
      width: 100%;
      margin: 0 auto;
    }

  }

  &__book-details {
    @include flex-all-sb;

    font-weight: 600;
    font-size: 24px;
    line-height: 21px;
    color: var(--colour-title);
  }

  &__book-price {
    margin: 0;
    padding-bottom: 30px;

    span {
      font-weight: 400;
      font-size: 18px;
      line-height: 21px;
    }

  }

  &__book-rating {
    font-weight: 600;
    font-size: 20px;
    line-height: 21px;
    position: relative;
    align-self: flex-start;
    margin: 0;
    padding-left: 25px;

    &::before {
      content: '';
      position: absolute;
      background-image: url("../img/rating--star.svg");
      height: 20px;
      width: 20px;
      top: 50%;
      left: 0;
      transform: translateY(-50%);
    }
  }

  &__players {
    grid-column: 9 span;
    margin-top: -220px;

    @include tablet {
      grid-column: 8 span;
    }

    @include small-tablet {
      grid-column: 12 span;
      margin-top: 60px;
    }

  }

  &-select__box {
    background-color: var(--colour-bg);
    border: none;
    font-weight: 400;
    font-size: 16px;
    line-height: 16px;
    padding: 5px 5px 5px 0;
    color: var(--colour-text);
  }

  &__label {

    padding: 8px 15px;
    margin-bottom:20px;
    background-color: var(--colour-bg);
    border-radius: 10px;

    &::before {
      display: none;
    }

    &:not(:last-child) {
      margin-right: 0;
    }

    @include mid-mobile {
      width: 100%;
    }

  }

  &__input {
    background-color: var(--colour-bg);
    padding: 5px 5px 5px 0;
  }

  &__btn {
    width: 100%;
  }

  &__section-title {
    font-weight: 600;
    font-size: 32px;
    line-height: 110%;
    margin: 0;
    padding-bottom: 30px;
    color: var(--colour-title);

    @include small-tablet {
      font-size: 28px
    }

  }

  &__amenities {
    grid-column: 9 span;
    margin-top: 60px;
    margin-right: 20px;

    @include big-tablet-min {
      margin-right: 0;
    }

    @include small-tablet {
      margin-right: 0;
      grid-column: 12 span;
    }
  }

  &__amenities-items {
      @include flex-v-center;

      justify-content: flex-start;
      flex-wrap: wrap;
  }

  &__amenities-item {
    @include flex-v-center;

    width: 30%;
    margin-bottom: 20px;

    @include mid-mobile {
      width: 40%;
    }
  }


  &__amenities-item-title {
    font-weight: 400;
    font-size: 16px;
    line-height: 16px;
    color: var(--colour-title);
    margin: 3px 0 0 0;
    padding-left: 10px;
  }

  &__things {
    grid-column: 9 span;
    margin-top: 60px;
    margin-right: 20px;

    @include big-tablet-min {
      margin-right: 0;
    }


    @include small-tablet {
      margin-right: 0;
      grid-column: 12 span;
    }

  }

  &__things-items {
    padding-left: 20px;
    margin: 0;
  }

  &__things-item {
    font-weight: 400;
    font-size: 16px;
    line-height: 140%;
    padding-bottom: 8px;
    color: var(--colour-text);
  }

  &__descr {
    grid-column: 9 span;
    margin-top: 60px;
    margin-right: 20px;


    @include small-tablet {
      margin-right: 0;
      grid-column: 12 span;
    }

  }

  &__descr-text {
    font-weight: 400;
    font-size: 16px;
    line-height: 140%;
    color: var(--colour-text);
    margin: 0;
    padding-bottom: 16px;
  }

  &__map {
    grid-column: 12 span;
    margin-top: 60px;
    margin-bottom: 60px;
    margin-right: 20px;

    @include big-tablet-min {
      margin-right: 0;
    }

    @include small-tablet {
      margin-right: 0;
    }

  }

  &__map-item {
    max-width: 1200px;
    width: 100%;
    border-radius: 10px;
  }

  &__reviews {
    grid-column: 12 span;
    margin-bottom: 30px;
  }

  &-reviews__right {
    max-width: 1200px;
    width: 100%;
  }

  &-reviews__item {
    margin-right: 20px;
  }

}
