// Form Input CSS

.input {

  height: 50px;
  padding: 8px 14px;
  margin-bottom: 30px;
  width: 100%;
  max-width: 380px;
  border-radius: 5px;
  border: none;
  background-color: var(--colour-white);
  filter: drop-shadow(3px 4px 10px rgba(232, 232, 232, 0.8));

  &::placeholder {
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: var(--colour-text);
  }

  &__error{
    border: 1px solid var(--colour-red);
  }

  &__error:focus {
    outline: var(--colour-red);
  }

  &__error:focus-visible {
    outline: var(--colour-red);
  }

}
