// Log in CSS

.sign {

  &__logo {
    margin-bottom: 30px;

    @include mid-tablet {
      align-self: center;
      margin-bottom: 40px
    }

  }

  &__main {
    @include flex;

    justify-content: space-between;
    margin: 30px auto;

    @include mid-tablet {
      justify-content: center
    }

  }

  &__left {
    @include flex;

    justify-content: space-between;
    flex-direction: column;
  }

  &__logo {
    flex-grow: 1;
  }

  &__form {
    flex-grow: 1;
    max-width: 380px;
  }

  &__title {
    font-weight: 600;
    font-size: 40px;
    line-height: 110%;
    color: var(--colour-title);
    margin: 0;
    padding-bottom: 8px;

    @include mid-tablet {
      font-size: 32px;
    }
  }

  &__text {
    font-weight: 400;
    font-size: 20px;
    line-height: 140%;
    color: var(--colour-title);
    margin: 0 0 60px 0;

    @include mid-tablet {
      font-size: 18px;
      margin-bottom: 40px;
    }
  }

  &__link {
    font-weight: 600;
    margin-left: 5px;
    color: var(--colour-title);
  }

  &__right {

    @include mid-tablet {
      display: none;
    }
  }

  &__right-img {
    border-radius: 10px;
    max-width: 520px;
  }

  &__btn {
    width: 100%;
    font-weight: 400;
    font-size: 16px;
    line-height: 16px;
    text-align: center;
    padding: 12px 24px;
    color: var(--colour-white);
    background-color: var(--colour-green);
    box-shadow: 2px 4px 10px rgba(122, 190, 112, 0.5);
    transition: box-shadow 0.3s ease-in-out;
    border-radius: 5px;
    margin-bottom: 30px;

    &:hover {
      box-shadow: 2px 4px 10px rgba(122, 190, 112, 0);
    }

  }

  &__with {
    margin-top: 30px;
    text-align: center;
  }

  &__with-title {
    position: relative;
    margin-bottom: 30px;
    font-weight: 600;
    font-size: 16px;
    line-height: 19px;
    color: var(--colour-text);

    &::before {
      content: "";
      position: absolute;
      height: 1px;
      width: 130px;
      top: 50%;
      left: 0;
      transform: translateY(-50%);
      background-color: var(--colour-text);

      @include md-mobile {
        width: 80px;
      }
    }

    &::after {
      content: "";
      position: absolute;
      height: 1px;
      width: 130px;
      top: 50%;
      right: 0;
      transform: translateY(-50%);
      background-color: var(--colour-text);

      @include md-mobile {
        width: 80px;
      }
    }

  }

  &__with-box {
    @include flex-all-center;


  }

  &__with-item {
    position: relative;
    height: 40px;
    width: 40px;
    border-radius: 5px;
    background: var(--colour-gradient-green);
    box-shadow: 2px 4px 10px rgba(122, 190, 112, 0.5);
    transition: box-shadow 0.3s ease-in-out;

    &:not(:last-child) {
      margin-right: 30px;
    }

    &:hover {
      box-shadow: 2px 4px 10px rgba(122, 190, 112, 0);
    }

  }

  &--google {

    &::before {
      content: "";
      position: absolute;
      background-image: url("../img/sign--google.svg");
      top: 24%;
      left: 20%;
      height: 24px;
      width: 24px;
    }

  }

  &--facebook {

    &::before {
      content: "";
      position: absolute;
      background-image: url("../img/sign--facebook.svg");
      top: 24%;
      left: 20%;
      height: 24px;
      width: 24px;
    }

  }


}
