.tutorials {

  @include flex-all-center;

  flex-direction: column;
  padding: 60px 0;
  border-radius: 10px;
  background: var(--colour-gradient-green);

  &__wrapper {
    height: 340px;

    @include big-mobile {
      height: 300px;
    }

    @include sm-mobile {
      height: 260px;
    }

  }

  &__title {
    margin: 0;
    padding-bottom: 8px;
    font-weight: 600;
    font-size: 56px;
    line-height: 110%;
    text-align: center;
    color: var(--colour-white);

    @include md-mobile {
      font-size: 36px;
    }
  }

  &__subtitle {
    margin: 0;
    font-weight: 400;
    font-size: 20px;
    line-height: 140%;
    text-align: center;
    color: var(--colour-white);

    @include md-mobile {
      font-size: 20px;
    }
  }

  &__slide-title {
    padding-bottom: 0;
  }

  &__nav {
    padding-bottom: 30px;
  }

  &__bottom {
    transform: translateY(0);
    padding: 10px 0 0 0;
    background-color: transparent;
  }

  &__img {
    max-height: 200px;
    border-radius: 10px;
  }

  &__info {
    margin: 8px 0 0 0;
  }

  &__time::before {
    background-image: url("/img/icon--time.svg");
  }

  &__views::before {
    background-image: url("/img/icon--views.svg");
    width: 16px;
    height: 16px;
  }


}
