
.homepage .header {
  position: absolute;
  left: 50%;
  top: 0;
  transform: translateX(-50%);

  @include tablet {

    position: relative;
    left: auto;
    transform: translateX(0);
    padding: 30px 0;
    background: var(--colour-gradient-green);

  }

  &__container {

    @include min-desktop {

      padding: 0 5%;
    }
  }

}


.header {
  position: relative;
  padding: 50px 0;
  max-width: var(--container-width);
  width: 100%;
  margin: 0 auto;

  &__container {
    @include flex-all-sb;

  }

}

