// About CSS

.about {

  padding-top: 140px;
  background-color: var(--colour-bg);
  text-align: center;

  @include mid-tablet {
    padding-top: 80px;
  }

  &__content {
    margin: 0 auto;
    max-width: 720px;
    width: 100%;

    @include mid-tablet {
      max-width: 600px;
    }

  }

  &__title {
    padding-bottom: 16px;
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    justify-content: center;
    margin-top: 40px;
    gap: 20px;
  }

  &__item {
    grid-column: 4 span;
    justify-self: center;
    position: relative;
  }

  &__item:not(:last-child)::after{
    content: '';
    position: absolute;
    background-image: url(/img/about--line.svg);
    background-repeat: no-repeat;
    background-size: contain;
    width: 160px;
    height: 60px;
    left: 200%;
    top: 20%;

    @include big-tablet {

      left: 190%;

    }

    @include mid-tablet {
      background-image: none;
    }

  }

  &__item:nth-child(2) {
    &::after {
      background-image: url(/img/about-line-reverse.svg);
      top: 30%;

      @include mid-tablet {
        background-image: none;
      }
    }
  }

  &__box {
    padding: 18px 20px;
    background: var(--colour-gradient-green);
    border-radius: 10px;

    @include mid-mobile  {
      padding: 14px 16px;
    }
  }

  &__subtitle {
    margin: 0;
    padding-top: 8px;
    font-weight: 600;
    font-size: 20px;
    line-height: 110%;
    color: var(--colour-title);
  }


}
