//  Burger CSS

.burger {
  position: relative;
  width: 40px;
  height: 40px;
  z-index: 10000;
  display: none;

  @include mid-tablet {
    display: block;
    margin-top: -4px;
  }

  &--active {

    .burger__line:first-child {
      margin: 0;
      transform: rotate(45deg);
    }
    .burger__line:nth-child(2) {
      visibility: none;
      opacity: 0;
    }
    .burger__line:last-child {
      margin: 0;
      transform: rotate(-45deg);
    }

  }

  &__line {
    position: absolute;
    left: 50%;
    top: 50%;
    width: 26px;
    height: 2px;
    border-radius: 5px;
    background-color: var(--colour-green);
    transform: translate(-50%,50%);
    transition: transform 0.3s ease-in-out, opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;

    &:first-child {
      margin-top: -8px;
    }

    &:nth-child(2) {
      top: 50%;
      transform: translate(-50%,50%);
    }

    &:last-child {
      margin-top: 8px;
    }
  }

}

.burger-close {
  position: absolute;
  right: var(--container-offset);
  top: 30px;
  width: 40px;
  height: 40px;
}

// Homepage Styles CSS

.homepage .burger {

  &__line {
    background-color: var(--colour-white);
  }

}
