// Chat CSS

.chat {

  display: grid;
  gap: 20px;
  grid-template-columns: repeat(12, 1fr);

  &__left {
    grid-column: 3 span;
    padding: 20px;
    background-color: var(--colour-white);
    height: 100vh;
    border-radius: 10px;
    box-shadow: 3px 4px 10px rgba(232, 232, 232, 0.8);
    overflow-y: scroll;

    &::-webkit-scrollbar {
      display: none;
    }

    @include big-tablet-min {
      grid-column: 4 span;
    }

    @include big-tablet-min {
      grid-column: 5 span;
    }

    @include small-tablet {
      grid-column: 12 span;
    }

  }

  &__title {
    font-weight: 600;
    font-size: 32px;
    line-height: 32px;
    margin: 0;
    padding-bottom: 30px;
    color: var(--colour-title);
  }

  &__form {
    @include flex-all-center;

    justify-content: space-between;
    padding: 8px;
    margin-bottom: 30px;
    border-radius: 10px;
    background-color: var(--colour-bg);
  }

  &__input {
    font-size: 16px;
    line-height: 16px;
    margin: 0 8px 0 0;
    color: var(--colour-title);
    border: none;
    background-color: transparent;

    &:focus {
      outline: none;
    }

    &:focus-visible {
      outline: none;
    }

  }

  &__btn {
    margin-right: 16px;
  }

  &__btn-img path {
    stroke: var(--colour-text);
  }

  &__item {
    margin-right: 0;
  }

  &__item-right {
    margin-top: 8px;
    align-self: flex-start;
  }

  &__info-text {
    overflow: hidden;
    max-width: 11ch;
    white-space: nowrap;
  }

  &__time {
    margin: 0;
  }


  &__right {
    grid-column: 9 span;
    padding: 20px;
    background-color: var(--colour-white);
    height: 100vh;
    border-radius: 10px;
    box-shadow: 3px 4px 10px rgba(232, 232, 232, 0.8);

    @include big-tablet-min {
      grid-column: 8 span;
    }
    @include big-tablet-min {
      grid-column: 7 span;
    }
    @include small-tablet {
      grid-column: 12 span;
      display: none
    }

  }

  &__right-box {
    @include flex-all-sb;

    height: 100%;
    flex-direction: column;
  }

  &__right-top{
    @include flex;

    flex-direction: column;
    width: 100%;
    overflow-y: scroll;

    &::-webkit-scrollbar {
      display: none;
    }
  }

  &__right-content:not(:last-child) {
    margin-bottom: 20px;
  }

  &__display-left {
    align-self: flex-start;
  }

  &__display-left {
    align-self: flex-end;
  }

  &__right-text {
    max-width: 380px;
    width: 100%;
    padding: 16px;
    margin: 0;
    border-radius: 10px;
    background-color: var(--colour-bg);
  }

  &__right-time {
    margin: 0;
    padding-top: 8px;
  }

  &__right-bottom {
    max-width: 630px;
    width: 100%;
    margin: 0 auto;
  }

  &__box-img {
    margin-bottom: 8px;
  }

  &__box-img:not(:last-child) {
    margin-right: 16px;
  }

  &__right-form {
    position: relative;
    padding: 8px 20px;
    border-radius: 10px;
    background-color: var(--colour-bg);
  }

  &__right-input {
    width: 90%;
    font-size: 16px;
    line-height: 16px;
    margin: 0 8px 0 0;
    color: var(--colour-title);
    border: none;
    background-color: transparent;

    &:focus {
      outline: none;
    }

    &:focus-visible {
      outline: none;
    }
  }

  &__right-btn {
    position: absolute;
    top: 8px;
    right: 8px;
  }


}

textarea {
  resize: none;
}
