// Search Form CSS

.search {
  @include flex-all-center;

  padding: 20px 20px;
  border-radius: 10px;
  background-color: var(--colour-white);

  @include mid-tablet {
    max-width: 600px;
    min-width: 280px;
    width: 100%;
    margin: 0 auto;
  }

  @include mid-mobile  {
    flex-wrap: wrap;
    justify-content: space-between;
  }

  &__label {
    @include flex;

    @include mid-mobile  {
      width: 50%;
      margin-bottom: 20px;
    }

    @include md-mobile  {
      width: 100%;
    }

    flex-direction: column;
    justify-content: flex-end;
    position: relative;
    font-weight: 600;
    font-size: 16px;
    line-height: 100%;

    padding-left: 38px;

    &--location::before {
      content: '';
      position: absolute;
      background-image: url(/img/icon--location.svg);
      width: 30px;
      height: 30px;
      left: 0px;
      top: 50%;
      transform: translateY(-50%);

    }
    &--date::before {
      content: '';
      position: absolute;
      background-image: url(/img/icon--date.svg);
      width: 30px;
      height: 30px;
      left: 0px;
      top: 50%;
      transform: translateY(-50%);

    }

  }

  &__label:not(:last-child) {
    margin-right: 10px;


    @include mid-mobile  {
      margin-right: 0;
    }

  }


  &__input {

      font-weight: normal;
      font-size: 16px;
      line-height: 100%;
      color: var(--colour-text);
      padding: 2px 0 0 0;
      margin-top: 2px;

      &:focus {
        outline: 1px solid var(--colour-text);
        border-radius: 5px;
      }

      &:focus-visible {
        outline: 1px solid var(--colour-text);
        border-radius: 5px;
      }

  }

  &__btn {

    font-weight: normal;
    font-size: 18px;
    line-height: 18px;
    color: var(--colour-white);
    padding: 10px 20px;
    border-radius: 5px;
    background-color: var(--colour-green);
    box-shadow: 2px 4px 10px rgba(122, 190, 112, 0.5);
    transition: box-shadow 0.3s ease-in-out;

    &:hover {
      box-shadow: 2px 4px 10px rgba(122, 190, 112, 0);
    }

    @include mid-mobile  {
      width: 100%;
    }


  }


}

// Hide calendar
input[type="date"]::-webkit-inner-spin-button,
input[type="date"]::-webkit-calendar-picker-indicator {
    display: none;
    -webkit-appearance: none;
}


// Search Page CSS

.search__page .search {
  max-width: 600px;
  margin: 80px auto;

  &__label {
    flex-grow: 1;
  }

  @include mid-tablet {
    margin: 30px auto;
  }

}
