.nav {

  margin: 0 auto;

  &__list {
    @include flex-v-center;

    @include mid-tablet {
      flex-direction: column;
    }

  }

  &__item:not(:last-child) {
    margin-right: 30px;

    @include mid-tablet {
      margin: 0px 0 30px 0;
    }
  }

  &__link {
    font-weight: 400;
    font-size: 18px;
    line-height: 18px;
    color: var(--colour-title);

    @include mid-tablet {
      font-size: 28px;
      line-height: 28px;
    }

  }

  &__container {
    @include flex;

    align-items: center;

    justify-content: end;
    width: 100%;

    @include mid-tablet {
      justify-content: center;
      align-items: center;
      flex-direction: column;
      position: fixed;
      left: 0;
      top: 0;
      z-index: 10000;
      overflow-y: auto;
      width: 100%;
      height: 100vh;
      background: var(--colour-gradient-bg);
      opacity: 0;
      visibility: hidden;
      transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out, background 0.3s ease-in-out;
    }
  }


}

.menu--active {
  opacity: 1;
  visibility: visible;
  background-color: var(--colour-bg);
}

.nav__right {
  position: relative;

  @include mid-tablet {
    margin-top: 10%;
  }

}

.nav__log {
  @include flex;

  &-item:not(:last-child) {
    margin-right: 30px;
  }

  &-link {
    font-weight: 400;
    font-size: 18px;
    line-height: 18px;
  }

  &--login {
    border: 1px solid var(--colour-green);
    background: rgba(31, 74, 185, 0);
    border-radius: 5px;
    padding: 8px 16px;
    color: var(--colour-green);
    transition: background 0.3s ease-in-out;
  }

  &--sign {
    border: 1px solid var(--colour-green);
    border-radius: 5px;
    padding: 8px 16px;
    background: var(--colour-green);
    color: var(--colour-white);
    transition: background 0.3s ease-in-out, border 0.3s ease-in-out, color 0.3s ease-in-out;
  }

  &--login {
    color: var(--colour-green);
  }

  &--sign {
    color: var(--colour-white);
  }

  &--login:hover {
    background: var(--colour-green);
    color: var(--colour-white);
  }

  &--sign:hover {
    color: var(--colour-green);
    background: transparent;
  }


}

.user {
  @include flex-all-center;

  @include mid-tablet {
    flex-wrap: wrap;
  }

  &__text{
    font-weight: 400;
    font-size: 18px;
    line-height: 18px;
    color: var(--colour-title);
    margin-right: 8px;
  }

  &__name {
    font-weight: 600;
  }

  &__img {
    position: relative;
    cursor: pointer;
  }

  &__pic:hover ~ .dropdown{
    visibility: visible;
    opacity: 1;
  }

}



.dropdown {
  position: absolute;
  opacity: 0;
  visibility: hidden;
  padding: 20px 20px;
  border-radius: 5px;
  background: var(--colour-white);
  top: 90px;
  right: 0;
  transition: visibility 0.3s ease-in-out, opacity 0.3s ease-in-out;
  z-index: 10000;

  @include mid-tablet {
    position: relative;
    opacity: 1;
    visibility: visible;
    top: 0;
    width: 100%;
    max-width: 200px;
  }

  &__list {
    @include flex;

    flex-direction: column;
  }

  &__link {
    display: block;
    color: var(--colour-text);

  }

  &__link:not(:last-child) {
    margin-bottom: 16px;
  }

  &__signout {
    color: var(--colour-red);
  }

  &:hover {
    visibility: visible;
    opacity: 1;
  }

}


// Homepage navigation
.homepage {

  .logo__img {
    filter: brightness(0) invert(1);

    @include mid-tablet {
      height: 40px;
      width: 100%;
    }

  }

  .nav {

    &__container {
      @include flex;

      @include mid-tablet {
        background: var(--colour-gradient-green);
      }
    }

    &__link {
      color: var(--colour-white);
    }

  }

  .nav__log {

    &--login {
      border: 1px solid var(--colour-blue);
    }

    &--sign {
      border: 1px solid var(--colour-blue);
      background: var(--colour-blue);
      color: var(--colour-white);
    }

    &--login {
      color: var(--colour-white);
    }

    &--sign {
      color: var(--colour-white);
    }

    &--login:hover {
      background: var(--colour-blue);
    }

    &--sign:hover {
      background: transparent;
    }

  }

  .user {

    &__text{
      color: var(--colour-white);
    }

  }

}

