/* stylelint-disable */

@mixin custom-checkbox {
  position: relative;
  width: 100%;

  &:not(:last-child) {
    margin-bottom: 8px;
  }

  &__field {
    appearance: none;
    position: absolute;
  }

  &__content {
    padding-left: 25px;
    cursor: pointer;
    font-weight: normal;
    font-size: 16px;
    line-height: 110%;
    color: var(--colour-text);

    &::before {
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      border: 1px solid var(--colour-text);
      border-radius: 5px;
      width: 16px;
      height: 16px;
      box-sizing: border-box;
    }

    &::after {
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      width: 16px;
      height: 16px;
      box-sizing: border-box;
      background-image: url("../img/filter--tick.svg");
      background-position: center;
      background-size: 10px 10px;
      background-repeat: no-repeat;
      opacity: 0;
      transition: opacity 0.3s ease-in-out;
    }
  }
}

.custom-checkbox__field:checked + .custom-checkbox__content::after {
  opacity: 1;
  background-color: var(--colour-green);
  border-radius: 5px;
  color: var(--colour-white);
}

