// Players CSS

.players {

  &__items {
    @include flex-all-sb;

    flex-wrap: wrap;
    @include big-tablet-min {
      justify-content: flex-start;
    }

    @include small-tablet {
      justify-content: flex-start;
    }

  }

  &__item {
    @include flex-all-sb;

    margin-right: 20px;
    margin-bottom: 20px
  }

  &__left {
    @include flex-all-center;
  }

  &__details {
    margin-left: 16px;
  }

  &__name {
    font-weight: 600;
    font-size: 20px;
    line-height: 110%;
    margin: 0;
    padding-bottom: 8px;
    color: var(--colour-title)
  }

  &__info {
    @include flex-v-center;

    justify-content: flex-start;
  }

  &__info-text {
    margin: 0;
    font-weight: 500;
    font-size: 16px;
    line-height: 1.1em;
    color: var(--colour-text);

    &:not(:last-child) {
      position: relative;
      padding-right: 20px;
    }

    &:not(:last-child)::after{
      content: '';
      position: absolute;
      top: 50%;
      right: 10px;
      height: 14px;
      width: 1.5px;
      border-radius: 2px;
      background-color: var(--colour-text);
      transform: translateY(-50%);
    }

  }

  &__rating{
    padding-left: 20px;

    &::before {
      height: 16px;
      width: 16px;
      background-position: center;
      background-size: cover;
      background-repeat: no-repeat;
    }

  }

  &__right {
    margin-left: 30px;
  }

  &__btn {
    position: relative;
    height: 32px;
    width: 32px;
    border-radius: 50%;
    background-color: var(--colour-white);
    transition: box-shadow 0.3s ease-in-out;
    box-shadow: 3px 4px 10px rgba(232, 232, 232, 0.8);

    &:hover {
      box-shadow: 3px 4px 10px rgba(232, 232, 232, 0);
    }

    &:not(:last-child) {
      margin-right: 8px;
    }

  }

  &__msg::after {
    content: '';
    position: absolute;
    height: 22px;
    width: 22px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-image: url("../img/icon--msg.svg");
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center center;
  }

  &__add::after {
    content: '';
    position: absolute;
    height: 24px;
    width: 24px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-image: url("../img/icon--add.svg");
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center center;
  }

  &__btn-more {
    font-weight: 400;
    font-size: 16px;
    line-height: 140%;
    color: var(--colour-text);
    border-bottom: 1px solid transparent;
    transition: border-bottom 0.3s ease-in-out;

    &:hover {
      border-bottom: 1px solid var(--colour-text);
    }

  }

}
