//  General Text CSS

.section {

  &__title {
    margin: 0;
    font-weight: 600;
    font-size: 40px;
    line-height: 110%;
    color: var(--colour-title);

    @include mid-mobile  {
      font-size: 28px;
    }
  }

  &__text {
    margin: 0 auto;
    font-weight: 400;
    font-size: 16px;
    line-height: 140%;
    color: var(--colour-text);
  }

}
