/* Add new listing */

.add-new{
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  margin: 0 auto;
  padding-top: 40px;
  padding-bottom: 3rem;
  max-width: 800px;
  width: 100%;

  @include big-mobile {
    padding-bottom: 0;
  }
}

.add-new__box{
  display: flex;
  flex-direction: column;
  width: 47%;
  margin-bottom: 30px;
  margin-right: 20px;

  & .input {
    margin: 0;
  }

  @include big-mobile {
    width: 100%;
    margin: 0 0 30px 0;
    align-items: center;
  }

}

.add-new__container{
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  gap: 0.5rem 1rem;
  align-items: center;
  flex-direction: row;
  width: 360px;
}

.add-new__pitch-label{
  font-size: 1.125rem;
  line-height: 1.1em;
  font-weight: 600;
  padding-bottom: 0.8rem;
  margin: 0;
}

.add-new__radio-label,
.add-new__checkbox-label{
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: start;
  position: relative;
  cursor: pointer;
  color: var(--colour-title);
}

.add-new__box--chk{
  padding-top: 2rem;
}

.add-new__radio-label::before{
  content: '';
  position: absolute;
  background-image: url('../img/rating--star.svg');
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
  width: 1rem;
  height: 1rem;
  left: 1.25rem;
}

.pitch__radio-label::before{
  display: none;
}

.add-new__radio-input,
.add-new__checkbox-input{
  display: none;
}

.add-new__radio-checkmark,
.add-new__checkbox-checkmark{
  position: relative;
  width: 1rem;
  height: 1rem;
  border: 1px solid var(--colour-green);
  border-radius: 50%;
  margin-right: 1.5rem;
  padding: 2px;
}

.add-new__checkbox-checkmark{
  border-radius: 5px;
  margin-right: 0.5rem;
  transition: all 0.5s ease;
  background-color: transparent;
}

.pitch__radio-checkmark{
  margin-right: 0.5rem;
}

.add-new__checkbox-checkmark--star{
  margin-right: 1.5rem;
}

.add-new__radio-checkmark::after{
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  opacity: 0;
  transition: all 0.3s ease-in-out;
  width: 0.6rem;
  height: 0.6rem;
  background-color: var(--colour-green);
  border-radius: 50%;
}

.add-new__checkbox-checkmark::after{
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  opacity: 0;
  transition: all 0.3s ease-in-out;
  background-image: url('../img/filter--tick.svg');
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  height: 70%;
  width: 70%;
  border-radius: 5px;
}

.add-new__checkbox-input:checked + .add-new__checkbox-checkmark{
  opacity: 1;
  background-color: var(--colour-green);
}

.add-new__radio-input:checked + .add-new__radio-checkmark::after,
.add-new__checkbox-input:checked + .add-new__checkbox-checkmark::after{
  opacity: 1;
}

.add-new__submit{
  height: fit-content;
  align-self: center;
  padding:1rem 3rem;
  margin: 1rem 0;
  border: none;
  cursor: pointer;
  font-size: 16px;
}
